const issue = {
    queryList() {
        return axios({
            url: '/repos/lifesinger/blog/issues',
            // url: '/repos/wefashe/blog/issues',
            method: 'get'
        })
    },
    queryDetail(id) {
        return axios({
            url: '/repos/lifesinger/blog/issues/'+id,
            method: 'get',
        })
    },
    marked(text) {
        return axios({
            url: '/markdown',
            method: 'post',
            data:{
                "text": text,
                "mode": 'gfm',
            }
        })
    },
}


// 
export default issue